<template>
  <div id="userEdit">
    <Modal
      v-model="edit"
      title="编辑"
      @on-ok="sysEdit">
      <Form ref="edit" :model="EditData" :rules="EditRule" :label-width="90" style="padding:0 50px;">
        <FormItem prop="id" label="ID：">
          {{EditData.id}}
        </FormItem>
        <FormItem prop="realName" label="姓名：">
          <Input v-model.trim="EditData.realName" placeholder="请输入姓名" size="default"/>
        </FormItem>
        <FormItem prop="phoneNumber" label="手机号码：">
          {{EditData.phoneNumber}}
        </FormItem>
        <FormItem prop="editCode" label="行政区划：">
          <Cascader :data="codeData" v-model="EditData.editCode" size="default" change-on-select></Cascader>
        </FormItem>
        <FormItem prop="departmentId" label="所属部门：">
          <Select v-model="EditData.departmentId" size="default">
            <Option v-for="item in departmentList" :value="item.id" :key="item.id">{{ item.name }}</Option>
          </Select>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="primary" @click="editSubmit">确定</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import { sysMobileUserEdit } from '@/api/systemManage'
import { getToken } from '@/utils/auth'
export default {
  name: 'UserEdit',
  data () {
    return {
      EditRule: {
        realName: [
          { required: true, message: '姓名不能为空', trigger: 'blur' }
        ],
        editCode: [
          { required: true, message: '行政区划不能为空', trigger: 'change', type: 'array' }
        ],
        departmentId: [
          { required: true, message: '所属部门不能为空', trigger: 'change', type: 'number' }
        ]
      },
      /* 编辑框 */
      edit: false,
      /* 编辑数据 */
      EditData: {}
    }
  },
  props: {
    /* 行政区划数据 */
    codeData: {},
    /* 部门列表 */
    departmentList: {},
    /* 编辑行原数据 */
    data: {}
  },
  watch: {
    data () {
      this.dataHandle()
    }
  },
  mounted () {
  },
  methods: {
    /* axios: 修改数据 */
    sysEdit () {
      sysMobileUserEdit({
        accessToken: getToken(),
        validate: true,
        data: {
          updateBy: this.$store.getters.getloginInfor().userId,
          id: this.EditData.id,
          realName: this.EditData.realName,
          phoneNumber: this.EditData.phoneNumber,
          code: this.EditData.editCode[this.EditData.editCode.length - 1].toString(),
          department: this.EditData.departmentId
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.$emit('getTableData')
          this.$Notice.success({ title: '修改成功' })
        } else {
          this.$Notice.error({ title: '修改失败' })
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 编辑框默认值数据处理 */
    dataHandle () {
      this.EditData.id = this.data.id
      this.EditData.realName = this.data.realName
      this.EditData.phoneNumber = this.data.phoneNumber
      // this.EditData.departmentId = this.data.departmentId
      this.$set(this.EditData, 'departmentId', this.data.departmentId)
      let arr = []
      this.getDefaultCode(this.codeData, this.data.code, arr)
    },
    /* 设置编辑框行政区划默认值 */
    getDefaultCode (data, code, arr) {
      for (let i in data) {
        if (data[i].value === code) {
          arr.push(data[i].value)
          this.EditData.editCode = JSON.parse(JSON.stringify(arr))
        } else {
          arr.push(data[i].value)
          if (data[i].children.length === 0) {
            arr.pop()
          }
          this.getDefaultCode(data[i].children, code, arr)
        }
      }
    },
    /* 修改表单验证 */
    editSubmit () {
      this.$refs['edit'].validate((valid) => {
        if (valid) {
          this.sysEdit()
          this.edit = false
        }
      })
    }
  }
}
</script>

<style scoped lang="scss">
</style>
