<template>
  <div id="user_add">
    <Modal
      v-model="build"
      title="新建"
      @on-cancel="AddData = {}"
    >
      <Form ref="add" :model="AddData" :rules="AddRule" :label-width="90" style="padding:0 50px;">
        <FormItem prop="addRealName" label="姓名：">
          <Input v-model.trim="AddData.addRealName" placeholder="请输入姓名" size="default"/>
        </FormItem>
        <FormItem prop="addPhoneNumber" label="手机号码：">
          <Input v-model.trim="AddData.addPhoneNumber" placeholder="请输入手机号码" size="default"/>
        </FormItem>
        <FormItem prop="addCode" label="行政区划：" >
            <Cascader :data="codeData" v-model="AddData.addCode" size="default" change-on-select></Cascader>
        </FormItem>
        <FormItem prop="addDepartment" label="所属部门：">
            <Select v-model="AddData.addDepartment" size="default">
              <Option v-for="item in departmentList" :value="item.id" :key="item.id">{{ item.name }}</Option>
            </Select>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="primary" @click="addSubmit">确定</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import { sysMobileUserAdd } from '@/api/systemManage'
import { getToken } from '@/utils/auth'
export default {
  name: 'UserAdd',
  data () {
    return {
      /* 添加数据 */
      AddData: {
        addRealName: '',
        addPhoneNumber: '',
        addCode: [],
        addDepartment: ''
      },
      AddRule: {
        addRealName: [
          { required: true, message: '姓名不能为空', trigger: 'blur' }
        ],
        addPhoneNumber: [
          { required: true, message: '手机号不能为空', trigger: 'blur' },
          { pattern: /^1[34578]\d{9}$/, message: '请填写正确的手机号码', trigger: 'blur' }
        ],
        addCode: [
          { required: true, message: '行政区划不能为空', trigger: 'change', type: 'array' }
        ],
        addDepartment: [
          { required: true, message: '所属部门不能为空', trigger: 'change', type: 'number' }
        ]
      },
      /* 新建框 */
      build: false
    }
  },
  props: {
    /* 行政区划数据 */
    codeData: {},
    /* 部门列表 */
    departmentList: {}
  },
  mounted () {
  },
  methods: {
    /* axios: 添加数据 */
    sysAdd () {
      sysMobileUserAdd({
        accessToken: getToken(),
        validate: true,
        data: {
          createBy: this.$store.getters.getloginInfor().userId,
          realName: this.AddData.addRealName,
          phoneNumber: this.AddData.addPhoneNumber,
          code: this.AddData.addCode[this.AddData.addCode.length - 1].toString(),
          department: parseInt(this.AddData.addDepartment)
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.$emit('getTableData')
          this.$Notice.success({ title: '添加成功' })
          this.build = false
          this.AddData = {}
        } else {
          this.$Notice.error({ title: res.data.msg })
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 添加表单验证 */
    addSubmit () {
      this.$refs['add'].validate((valid) => {
        if (valid) {
          this.sysAdd()
        }
      })
    }
  }
}
</script>

<style scoped lang="scss">
</style>
